.library{
    font-size: 4vh !important;
    margin-top:4%;
    margin-left: 5%;
}

.list{
    margin-top: 4% !important;
}
li{
    font-size: 1.8rem;
    margin-left: 5% !important;
    margin-top: 6% !important;
 }

 li:hover{
     font-size: 2.1rem;
     text-decoration: underline;
     cursor: pointer;
 }

.marsweather{
    text-align: center;
    margin-top: 15% !important;
    font-size: 2rem;
 }

 .container{
    margin-top: 7% !important;
 }

 .card_container{
     font-size: 3vh;
     margin-left: -1%;
     height: 30% !important;
     width: 50% !important;
 }

 .title_solar{
     margin-top: 4%;
     font-size: 2vh;
 }

 .temp_card{
     margin-top: 10%;
     margin-left: 11% !important;
     height: 50% !important;
     width: 80% !important;
     margin-bottom: 10% !important;
 }

 .title{
     font-size: 3vh;
     margin-top: 10%;
 }

 .temp_grid{
     margin-left: 50% !important;
 }

 @media only screen and (max-width : 770px)
 {

    .lib_title{
        margin-left: 5% !important;
    }

    .title{
        font-size: 1.2vh;
    }

    h3{
        font-size: 1vh;
    }

    .season{
        font-size: 1.2vh;
    }

     .title_solar{
         font-size: 1vh;
     }

    .list{
        margin-top: -1% !important;
        margin-left: 2% !important;
    }

    .library{
        font-size: 1vh !important;
        margin-left: 0% !important;
    }

     li{
         font-size: 1vh;
         margin-top: 2% !important;
     }

     li:hover{
         font-size: 1.2vh !important;
     }

     .marsweather{
         margin-top: 5% !important;
         font-size: 1vh;
     }
 }