.container{
    margin-top: 4% !important;
}

.mars_rover_photo{
    margin-top: 5%;
}

.mars_rover_photo>p{
    font-size: 2rem;
}

.image_container{
    height: 300px;
    width: 300px;
}

.image_header{
    margin-top: 2%;
    font-size: 2vh;
}

.camera_content{
    height: 50px;
    width: 200px;
    font-size: 1.3vh;
}

.date{
    margin-top: -9px;
}

@media only screen and (max-width : 770px)
{
    .container{
        margin-top: -10%;
    }
    .mars_rover_photo{
        margin-top: 50%;
    }

    .mars_rover_photo>p{
        margin-top: -30px;
    }

    .image_container{
        height: 80px;
        width: 80px;
    }

    .image_header{
        font-size: 0.7vh;
    }

    .camera_content{
        font-size: 0.4vh;
        margin-top: -12px;
        padding: 0px;
        height: 10px;
        width: 60px;
    }
    .camera_content>p{
        margin-top: -10px;
    }
    .date{
        margin-top: 2px;
    }
    .camera_name{
        margin-top: -15px;
    }
}